<template>
  <div class="card">
    <div class="card-content">
      <div class="columns is-multiline is-mobile">
        <word-item
          v-for="word in words"
          :key="word.name"
          :name="word.name"
          :emoji="word.emoji"
          :category="name"
          :translation="getTranslation(word.translations)"
          v-if="getTranslation(word.translations) !== 'no translation' || !translatedOnly"
        ></word-item>
      </div>
    </div>
  </div>
</template>

<script>
import WordItem from '@/components/WordItem';

export default {
  name: 'word-category',
  props: {
    name: String,
    words: Array,
    language: String,
    translatedOnly: Boolean,
  },
  components: {
    WordItem,
  },
  methods: {
    getTranslation(translations) {
      return Object.prototype.hasOwnProperty.call(translations, this.language) ? translations[this.language] : 'no translation';
    },
  },
};
</script>

<style scoped>
.card {
  margin-top: 2rem;
  border-top: 3px solid #7957d5;
  ;
}
</style>


