<template>
  <div class="tags">
    <a @click="toggleCategory('all')" :class="{'is-active' : choosed.includes('all')}" class="tag">all</a>
    <a class="tag"
      v-for="category in categories"
      v-bind:key="'c'+category.name"
      @click="toggleCategory(category.name)"
      :class="{'is-active' : choosed.includes(category.name)}"
    >
      {{ category.name }}
    </a>
  </div>
</template>

<script>
export default {
  name: 'category-picker',
  props: {
    categories: Array,
    choosedCategories: Array,
  },
  data: () => ({
    choosed: [],
  }),
  created() {
    this.choosed = this.choosedCategories;
  },
  methods: {
    toggleCategory(name) {
      if (this.choosed.includes(name)) {
        this.choosed = this.choosed.filter(category => category !== name);
      } else {
        this.choosed.push(name);
      }
    },
  },
  watch: {
    choosed() {
      if (this.choosed.length === 0) {
        this.choosed.push('all');
      }
      this.$emit('update:choosedCategories', this.choosed);
    },
  },
};
</script>

<style scoped>
.is-active {
  background-color: #7957d5;
  color: whitesmoke;
}
</style>



