<template>
  <div class="column">
    <word-modal
      :emoji="emoji"
      :translation="translation"
      :category="category"
      :isActive.sync="isActive"
    ></word-modal>
    <a @click="isActive = true">
      <span>{{emoji}}</span>
    </a>
  </div>
</template>

<script>
import WordModal from '@/components/WordModal';

export default {
  name: 'word-item',
  props: {
    name: String,
    emoji: String,
    translation: String,
    category: String,
  },
  components: {
    WordModal,
  },
  data: () => ({
    isActive: false,
  }),
};
</script>

<style scoped>
span {
  font-size: 2.5rem;
}
.modal {
  -webkit-animation-duration: 0.5s;
  -webkit-animation-delay: 0s;
}
</style>



