<template>
  <div>
    <word-info
      :wordCount="wordCount"
      :translatedWordCount="translatedWordCount"
    ></word-info>
    
    <div class="card"> 
      <category-picker
        :categories="categories"
        :choosedCategories.sync="choosedCategories"
      ></category-picker>
      <a v-if="translatedWordCount > 0" class="button" @click="translatedOnly = !translatedOnly">
        Show {{ translatedOnly ? 'all' : 'translated' }}
      </a>
    </div>

    <word-category
      v-for="category in categories"
      v-if="choosedCategories.includes('all') || choosedCategories.includes(category.name)"
      v-bind:key="category.name"
      :name="category.name"
      :words="category.words"
      :language="language"
      :translatedOnly="translatedOnly"
    ></word-category>

    <a class="button is-light to-top" v-scroll-to="'#app'">Scroll to top</a>
  </div>
</template>

<script>
import { getCategories, getWordCount, getTranslatedWordCount } from '@/api';
import WordInfo from '@/components/WordInfo';
import CategoryPicker from '@/components/CategoryPicker';
import WordCategory from '@/components/WordCategory';

export default {
  name: 'language',
  components: {
    WordInfo,
    WordCategory,
    CategoryPicker,
  },
  data: () => ({
    translatedOnly: false,
    categories: [],
    wordCount: 0,
    translatedWordCount: 0,
    choosedCategories: ['all'],
  }),
  created() {
    this.categories = getCategories();
    this.wordCount = getWordCount();
    this.translatedWordCount = getTranslatedWordCount(this.language);
  },
  watch: {
    language() {
      this.translatedWordCount = getTranslatedWordCount(this.language);
      this.translatedOnly = false;
    },
  },
  computed: {
    language() {
      return this.$route.params.name;
    },
  },
};
</script>

<style scoped>
.card {
  padding: 0.5rem;
}

.to-top {
  margin-top: 1rem;
  width: 100%;
}
</style>


