<template>
  <div class="modal animated fadeIn" :class="{ 'is-active': isActive }">
    <div @click="closeModal" class="modal-background"></div>
    <div class="modal-content">
      <div class="card">
        <div class="card-image">
          <span>{{ emoji }}</span>
        </div>
        <div>
          <div class="media">
            <p class="title is-1">{{ translation }}</p>
          </div>
          <div class="content">
            <a>#{{ category }}</a>
          </div>
        </div>
      </div>
    </div>
    <button @click="closeModal" class="modal-close is-large" aria-label="close"></button>
  </div>
</template>

<script>
export default {
  name: 'word-modal',
  props: {
    isActive: Boolean,
    translation: String,
    emoji: String,
    category: String,
  },
  methods: {
    closeModal() {
      this.$emit('update:isActive', false);
    },
  },
};
</script>

<style scoped>
.card-image,
.content {
  text-align: center;
}

.title {
  margin-left: auto;
  margin-right: auto;
}

span {
  font-size: 3rem;
}

div {
  font-size: 1.5rem;
}

a {
  color: #7957d5;
}

.card-content {
  padding: 0;
}
</style>


