<template>
  <div class="card has-text-centered">
    <b>{{ translatedWordCount }}</b> of <b>{{ wordCount }}</b> words translated
  </div>
</template>

<script>
export default {
  name: 'word-info',
  props: {
    wordCount: Number,
    translatedWordCount: Number,
  },
};
</script>

<style scoped>
.card {
  padding: 0.5rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0.5rem;
}
</style>


